import { FileRowType, ServerFile } from "@rp/common/utils"
import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"
import { PatchFile } from "../file.types"

type PatchFileResponse = Pick<
  ServerFile,
  "file_id" | "file_name" | "status" | "raw_size" | "upload_ts" | "shared_with"
>

const patch = async (
  { fileId, fields }: PatchFile,
  originalFile: FileRowType,
): Promise<FileRowType> => {
  const { data } = await axiosClient.patch<PatchFileResponse>(
    `${FILES_BASE_URL}${FILES_ROOT}/${fileId}/metadata`,
    fields,
  )
  return {
    ...originalFile,
    name: data.file_name,
    sharedWith: data.shared_with,
  }
}

export { patch }
